import { ComponentProps, forwardRef, useState } from 'react';
import TextField, { UserTextField } from './TextField';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEye, faEyeSlash } from '@fortawesome/free-solid-svg-icons';

type PasswordTextFieldProps = Omit<ComponentProps<typeof TextField>, 'icon' | 'type'>;

/**
 * Same as `TextField` Component but with special password related features.
 * Shows an Icon at the end of the input to toggle if password is hidden or not.
 * @returns
 */
const PasswordTextField = forwardRef<HTMLInputElement, PasswordTextFieldProps>((props, ref) => {
	const [showPassword, setShowPassword] = useState(false);

	return <UserTextField {...props} ref={ref} type={showPassword ? 'text' : 'password'} rightButtonLabel={showPassword ? <FontAwesomeIcon icon={faEye} style={{color:'black',  width: '16px', height: '16px' }} /> : <FontAwesomeIcon icon={faEyeSlash} style={{color:'var(--app-color-gray-light)',  width: '16px', height: '16px' }} />} rightButtonAction={() => setShowPassword(c => !c)} rightButtonClass={''} />;
});

export default PasswordTextField;
