import { useEffect, memo, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { restartAxiosAbort, setOnBoardingComplete } from '../../reduxStore/actions/flowActions';
import cls from './Login.module.scss';
import { LoginForm } from './LoginForm/LoginForm';
import { isMobile } from 'react-device-detect';
import { resetApplication } from '../../services/auth.service';
import { getMasterData, getMobileData } from '../../services/master.service';
import { store } from '../../reduxStore/store';
import { APP_NAME, FLOWHERO } from '../../shared/utility';

const Login = memo(props => {
	const { title } = props;

	const navigate = useNavigate();
	const dispatch = useDispatch();
	const { companyData, lastUpdates } = store.getState().flowReducer;
	const { authenticated } = store.getState().sessionReducer;
	const [company,] = useState(JSON.parse(localStorage.getItem('companyData')) ?? { 'logo': '' });
	document.title = `Login | ${APP_NAME}`;

	useEffect(() => {
		if (authenticated) {
			navigate('/flowhero');
		} else {
			dispatch(setOnBoardingComplete(false));
			dispatch(restartAxiosAbort())
			resetApplication();
		}


	}, []);

	return authenticated == false ? (
		<div style={{ display: 'flex', flexDirection: 'row', width: '100%', height: '98vh' }}>
			<div style={{ width: isMobile ? '100%' : '20%', padding: '20px', minWidth: '300px' }}>

				<div className={cls.headerTopBarContainer} style={{ fontSize: isMobile ? '12px' : '16px', marginTop: '20px' }} onClick={() => navigate('/flowhero')}>
					{!company?.logo && <p style={{ fontFamily: 'Montserrat' }}>{company?.display_name?.toUpperCase()}</p>}
					{company?.logo ?
						<img alt="lg" src={company?.logo} className={isMobile ? cls.logoUrlImgMobile : cls.logoUrlImg} />
						:
						isMobile ? FLOWHERO('30px', 'var(--app-color-gray-dark)', 'bold') :
							FLOWHERO('32px', 'var(--app-color-gray-dark)', 'bold')}
				</div>
				<LoginForm
					onLoginSuccess={data => {
						const { isSingleApp } = data;

						lastUpdates.componentsByModule = {};

						if (!(companyData?.status === 'trial_ended' || companyData?.status === 'inactive')) {
							if (isSingleApp === true && isMobile) {
								getMobileData();
								navigate('/mobile');
								return;
							} else {
								getMasterData();
								// getMobileData();
								// navigate('/mobile');
								// return;
							}
						} else if (companyData?.status === 'inactive') {
							getMasterData(true);
						} else {
							// navigate('/login');
							// return;
							getMasterData();
						}
						navigate('/');
					}}
					onResetPassword={() => navigate('/reset-password')}
				/>
			</div>


			<div style={{ width: '80%', display: isMobile ? 'none' : 'flex', backgroundColor: 'rgb(246,244,236)', justifyContent: 'flex-end', padding: '20px', height: '102vh' }} >
				<ul className={cls.navbaritems} style={{ display: isMobile ? 'none' : '', marginRight: '20px' }}>
					<li>
						<a href={company?.slug ? `#/pages/home/${company?.slug}` : "#/flowhero/"} className={cls.navbarlinks}>
							{`HOME`}
						</a>

					</li>
					<li>
						<a href="https://www.flowheroapps.com/#/"
							className={cls.navbarlinks}
							style={{
								cursor: 'pointer',

							}}
						>
							{`CONTACT US`}
						</a>
					</li>
					<li>
						<a href="#/legal" className={cls.navbarlinks}>
							{`PRIVACY`}
						</a>
					</li>

				</ul>




			</div>
		</div>
	) : (
		<div style={{ margin: 'auto', width: '100%', height: '500px', display: 'flex', alignContent: 'center', alignItems: 'center', justifyItems: 'center', justifyContent: 'center' }}>{FLOWHERO('30px', 'var(--app-color-gray-dark)', 'bold')}</div>
	);
});

export default Login;
