
import { yupResolver } from '@hookform/resolvers/yup';
import classNames from 'classnames';
import React, { ComponentProps, memo, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { first, take } from 'rxjs/operators';
import { useValidation } from '../../../hooks/useValidation';
import { setOnBoardingComplete, setShowMenu } from '../../../reduxStore/actions/flowActions';
import { setIsOnboarding, setOnBoardingModalsVisibility, setOnBoardingVisible } from '../../../reduxStore/actions/modalsActions';
import type { StoreType } from '../../../reduxStore/store';
import { userLogin } from '../../../services/auth.service';
import { Button } from '../../common';
import { Checkbox } from './FormElements/Checkbox';
import PasswordTextField from './FormElements/PasswordTextField';
import TextField, { ErrorMessage, UserTextField } from './FormElements/TextField';
import cls from './LoginForm.module.scss';
import { isMobile } from 'react-device-detect';
import { APP_NAME } from '../../../shared/utility';

const accessRestrictedMessage = `Please setup the company\nPlease contact comapny admin.\nBasic company information must be enroled\nBefore you will be granted access to ${APP_NAME}`;
type FormType = {
	email: string;
	password: string;
	rememberMe?: boolean;
};

export type LoginFormProps = {
	className: string;
	onLoginSuccess?: (data: any) => void;
	onResetPassword?: () => void;
} & ComponentProps<'form'>;

export const LoginForm = memo((props: LoginFormProps) => {
	const { onLoginSuccess, className, onResetPassword } = props;
	const dispatch = useDispatch();
	const { rememberMe } = useSelector((reduxStore: StoreType) => reduxStore.authReducer.sessionSettings);
	const [loading, setLoading] = useState(false);
	const [responseError, setResponseError] = useState('');
	const { schemas, yup } = useValidation();
	const {
		register,

		handleSubmit,
		formState: { errors },
		getValues,
	} = useForm<FormType>({
		mode: 'onSubmit',
		criteriaMode: 'firstError',
		resolver: yupResolver(
			yup.object().shape({
				email: schemas.email,
				password: schemas.password,
			})
		),
	});

	const onSubmit = (_data: FormType) => {
		setResponseError('');
		setLoading(true);
		const { email, password, rememberMe } = getValues();
		userLogin({ email: email, password: password, rememberMe: true })
			.pipe(take(1))
			.subscribe({
				next: data => {
					const { is_admin, onboarding_status } = data;

					if (onboarding_status === false) {
						if (is_admin === true) {
							dispatch(setOnBoardingVisible(true));
							dispatch(setIsOnboarding(true));
							dispatch(setOnBoardingModalsVisibility({ firstModalVisible: true }));
							dispatch(setShowMenu(false));
							setLoading(false);
						} else {
							setResponseError(accessRestrictedMessage);
							setLoading(false);
						}
					} else {
						dispatch(setOnBoardingVisible(false));
						dispatch(setIsOnboarding(false));
						dispatch(setOnBoardingComplete(true));
						setLoading(false);
						onLoginSuccess?.(data);
					}
				},
				error: error => {
					const extractedError = typeof error.data == 'string' ? error.data : error?.error ? error?.error : error?.data?.message;
					setResponseError((error.status === 422 || extractedError === 'Not authenticated') ? 'Invalid user name or password, please try again.' : extractedError);
					setLoading(false);
				},
			});
	};

	return (
		<form onSubmit={handleSubmit(onSubmit)} className={classNames(cls.host, className)} onClick={() => setResponseError('')}>
			<div className={cls.row} app-variant="first-row">
				<UserTextField style={{ backgroundColor: 'rgb(246,244,236)' }} autoComplete={rememberMe ? 'username' : 'new-password'} type="text" placeholder={'Email or Username'} defaultValue={localStorage.getItem('username') ?? ''} {...register('email')} />
			</div>
			<div className={cls.row} app-variant="second-row">
				<PasswordTextField style={{ backgroundColor: 'rgb(246,244,236)' }} autoComplete={rememberMe ? 'on' : 'off'} placeholder={'Password'} defaultValue={localStorage.getItem('password') ?? ''} {...register('password')} />
			</div>
			{(responseError || errors.password?.message || errors.email?.message) && <ErrorMessage>{`${responseError ?? ''} ${errors.password?.message ?? ''} ${errors.email?.message ?? ''} `}</ErrorMessage>}
			<Button
				disabled={loading}
				loading={loading}
				type={'submit'}
				title={'Sign in'}
				className={cls.signInButton}
			/>
			<div className={cls.row} app-variant="third-row">
				<br />
				<p className={cls.recoverPasswordButton} onClick={onResetPassword}>{isMobile ? 'Forgot Password' : 'Need to find your password?'}</p>
				<br />
			</div>

		</form>
	);
});
